import React, { Component } from 'react'
import CheckBoxes from "./CheckBoxes";
class MainsMaster extends Component {

    constructor(props){
        super(props);

        this.state = {
            data:{
                orangegrandMaster:0,
                kokamgrandMaster:0,
                lemongrandMaster:0,
                sarbatgrandMaster:0
            },
            total:0
        }
    }

    getBill(data){
        this.state.data.orangegrandMaster = data;
        this.setState({
            orangegrandMaster:data
        })
        console.log("getBill:"+data);
        console.log(this.state.data);
        this.getBillAll();
    }

    getBillKokam(data){
        const data1 = {...this.state.data,}
        this.state.data.kokamgrandMaster = data;
        this.setState({
            kokamgrandMaster:data
        })
        console.log("getBillKokam:"+data);
        console.log(this.state.data);
        this.getBillAll();
    }

    getBillLemon(data){
        this.state.data.lemongrandMaster = data;
        this.setState({
            lemongrandMaster:data
        })
        console.log("getBillLemon:"+data);
        console.log(this.state.data);
        this.getBillAll();
    }

    getBillSarbat(data){
        this.state.data.sarbatgrandMaster = data;
        this.setState({
            sarbatgrandMaster:data
        })
        console.log("getBillSarbat:"+data);
        console.log(this.state.data);
        this.getBillAll();
    }

    getBillAll(){
        let {orangegrandMaster,kokamgrandMaster,lemongrandMaster,sarbatgrandMaster} = this.state.data;
        let totalBill = orangegrandMaster + kokamgrandMaster + lemongrandMaster +sarbatgrandMaster;
        console.log(totalBill);
        this.setState({total:totalBill});
    }
    


    render() {
        let orangeBill = 0;
        let dynCls ="";
        this.state.data.orangegrandMaster > 100 ? dynCls="makered" : dynCls=""
        return (
            <div className="container">
                <div className="row">
                    <div className="col-2">
                    <CheckBoxes data={
                    {getBill:this.getBill.bind(this)}
                } name="Orange" mrp={15} color="orange" changeColor="green" names="orangeCheck"/>
                    </div>
                    <div className="col-2">
                    <CheckBoxes data={
                    {getBillKokam:this.getBillKokam.bind(this)}
                } name="Kokam" mrp={20} color="red" changeColor="green" names="kokamCheck"/>

                    </div>
                    <div className="col-2">
                    <CheckBoxes data={
                    {getBillLemon:this.getBillLemon.bind(this)}
                } name="Lemon" mrp={15} color="blue" changeColor="green" names="lemonCheck" />

                    
                    </div>
                    <div className="col-2">
                    <CheckBoxes data={
                    {getBillSarbat:this.getBillSarbat.bind(this)}
                } name="Sarbat" mrp={15} color="blue" changeColor="green" names="sarbatCheck" />
                    </div>
                </div>
                <h1 className="display-6">Parent</h1>
                <div className="display-2">
                <p className={this.state.data.orangegrandMaster > 1000 ? "makered" : "makegreen"}>Orange:{this.state.data.orangegrandMaster}</p>
                </div>
                <div className="display-3">
                <p>Kokam:{this.state.data.kokamgrandMaster}</p>
                </div>
                <div className="display-4">
                <p>Lemon:{this.state.data.lemongrandMaster}</p>
                </div>
                <div className="display-4">
                <p>Sarbat:{this.state.data.sarbatgrandMaster}</p>
                </div>                
                <h3>Total Bill : {this.state.total}</h3>
                <button className="btn btn-primary" onClick={this.getBillAll.bind(this)}>Bill</button>
            </div>
        )
    }
}
export default MainsMaster;