import axios from 'axios';
import React, { Fragment, useEffect, useState } from 'react';
import { Form, FormGroup, Button, Container, Input } from 'reactstrap';
import { toast } from 'react-toastify';
import base_url from '../api/bootapi';
import { FaSleigh } from 'react-icons/fa';
import "./loginReg.css";

const initialState = {
        id:'',
        title:'',
        description:'',
        phone:'',
        pincode:'',
        date:'',
        titleError:'',
        descriptionError:'',
        phoneError:'',
        pincodeError:'',
        dateError:'',
    loggedIn:false,
    tokenFound:false
}

class AddCourse extends React.Component {
    state = initialState;
    // useEffect(() => {
    //     console.log("successs");
    //        document.title = "Add New Course";
    //        loadFunction();
    // },[])

    // const [course,setCourse] = useState({});

    // state = {};

    constructor(props) {
        super(props);
        this.onChange = this.onChange.bind(this);
        this.handleForm = this.handleForm.bind(this);
        // this.pass1 = this.pass1.bind(this);
        // this.submitForm = this.submitForm.bind(this);
        const token = localStorage.getItem('token');
        let tokenFound = false;
        let loggedIn = true;
        
        if (token === null) {
            loggedIn = false;
            this.setState({loggedIn:false});
        }else{
            this.setState({loggedIn:true});
        }
        console.log(this.state);
    }

    componentDidMount(){
        console.log("successs");
           document.title = "Add New Course";
           this.loadFunction();
    }

    validate = () => {
        let titleError = '';
        let descriptionError = '';
        let phoneError = '';
        let pincodeError = '';
        let dateError = '';
        // const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);
        // if (!validEmailRegex.test(this.state.email)) {
        //     emailError = 'Invalid Email';
        //     document.getElementsByName("email")[0].classList.add("error");
        //     this.state.email = '';
        //     this.state.emailError = emailError;
        // }else{
        //     document.getElementsByName("email")[0].classList.remove("error");
        // }
        // if (!this.state.password || this.state.password.length < 8) {
        //     passError = "Invalid password";
        //     document.getElementsByName("password")[0].classList.add("error");
        //     this.state.password = '';
        //     this.state.passError = passError;
        // }else{
        //     document.getElementsByName("password")[0].classList.remove("error");
        // }

        if (!this.state.title.length > 0) {
            titleError = "Invalid Title";
            document.getElementsByName("title")[0].classList.add("error");
            this.state.title = '';
            this.state.titleError = titleError;
        }else{
            document.getElementsByName("title")[0].classList.remove("error");
        }

        if(!this.state.description.length > 0){
            descriptionError = "Invalid Description";
            document.getElementsByName("description")[1].classList.add("error");
            this.state.description = '';
            this.state.descriptionError = descriptionError;
        }else{
            document.getElementsByName("description")[1].classList.remove("error");
        }

        if(!this.state.phone.length>0){
            phoneError = "Invalid Phone";
            document.getElementsByName("phone")[0].classList.add("error");
            this.state.phone = '';
            this.state.phoneError = phoneError;
        }else{
            document.getElementsByName("phone")[0].classList.remove("error");
        }

        if(!this.state.pincode){
            pincodeError = "Invalid Phone";
            document.getElementsByName("pincode")[0].classList.add("error");
            this.state.pincode = '';
            this.state.pincodeError = pincodeError;
        }else{
            document.getElementsByName("pincode")[0].classList.remove("error");
        }

        if(!this.state.date){
            dateError = "Invalid Date";
            document.getElementsByName("date")[0].classList.add("error");
            this.state.date = '';
            this.state.dateError = dateError;
        }else{
            document.getElementsByName("date")[0].classList.remove("error");
        }

        if (titleError || descriptionError || phoneError || pincodeError || dateError) {
            this.setState({ titleError, descriptionError,phoneError,pincodeError,dateError });
            return false;
        }
        return true;
    }

    //form handler func
    handleForm(e){
        e.preventDefault();
        const isValid = this.validate();
        console.log(isValid);
        console.log(this.state);
        if(isValid){
            this.postDataToServer(this.state);
            this.setState(initialState);
        }
    }


    //post data to server
    postDataToServer = (data) => {
        axios.post(`/courses`, data).then(
            (response) => {
                console.log("success");
                toast.success("course has been added", {});
            },
            (error) => {
                console.log("error");
                toast.error("something went wrong", {});
            }
        );
    }

    loadFunction = () => {
        axios.get(`/courses`).then(
            (response) => {
                console.log("success");
                // this.setState({course:response.data});
            },
            (error) => {
                console.log("error");
            }
        )
    }

    onChange(e){
        console.log(this.state);
        if(e.target.name==="title"){
            let title = e.target.value;
            if(title){
                document.getElementsByName("title")[0].classList.remove("error");
            }else{
                document.getElementsByName("title")[0].classList.add("error");
            }
            
        }
        if(e.target.name === "description"){
            let description = e.target.value;
            if(description){
                document.getElementsByName("description")[1].classList.remove("error");
            }else{
                document.getElementsByName("description")[1].classList.add("error");
            }
        }

        if(e.target.name === "phone"){
            const re = /^[0-9\b]+$/;
            let phone = e.target.value;
            if(re.test(phone)){
                document.getElementsByName("phone")[0].classList.remove("error");
                this.setState({phone});
            }else{
                this.setState({phone:''});
                document.getElementsByName("phone")[0].classList.add("error")
            }
        }

        if(e.target.name === "pincode"){
            let pincode = e.target.value;
            if(pincode){
                document.getElementsByName("pincode")[0].classList.remove("error");
            }else{
                document.getElementsByName("pincode")[0].classList.add("error");
            }
        }

        if(e.target.name === "date"){
            let date = e.target.value;
            if(date){
                document.getElementsByName("date")[0].classList.remove("error");
            }else{
                document.getElementsByName("date")[0].classList.add("error");
            }
        }

        this.setState({
            [e.target.name]: e.target.value
        });
        console.log(this.state);
    }

    render(){

    return(
        <Fragment>
            <h1 className="text-center my-2" style={{color:'#fff'}}>Fill Course details</h1>
            <form onSubmit={this.handleForm}>
                
                <FormGroup hidden>
                    <label for="id">ID</label>
                    <input
                        className="form-control"
                        type="text"
                        placeholder="Enter id"
                        name="id"
                        id="id"
                        onChange={this.onChange}
                        value={this.state.id}
                    />
                </FormGroup>

                <FormGroup>
                    <label for="title">Course Title</label>
                    <input
                        className="form-control"
                        type="text"
                        placeholder="Enter title"
                        name="title"
                        id="title"
                        onChange={this.onChange}
                        value={this.state.title}
                    />
                </FormGroup>

                <FormGroup>
                    <label for="description">Course Description</label>
                    <textarea
                        className="form-control"    
                        type="textarea"
                        placeholder="Enter description"
                        name="description"
                        id="description"
                        style={{height:'150px'}}
                        onChange={this.onChange}
                        value={this.state.description}
                    />
                </FormGroup>
                <div className="row">
                    <div className="col-md-6">
                    <FormGroup>
                                    <label for="phone">Contact No.</label>
                                    <input
                                    className="form-control"
                                        type="text"
                                        placeholder="Enter Phone"
                                        name="phone"
                                        id="phone"
                                        maxLength="10"
                                        onChange={(e)=>{this.setState({phone:e.target.value.replace(/\D/,'')})}}
                                        value={this.state.phone}
                                    />
                                </FormGroup>

                    </div>
                    <div className="col-md-6">
                    <FormGroup>
                                    <label for="pincode">Pincode</label>
                                    <input
                                    className="form-control"
                                        type="text"
                                        placeholder="Enter Pincode"
                                        name="pincode"
                                        id="pincode"
                                        maxLength="6"
                                        onChange={(e)=>{this.setState({pincode:e.target.value.replace(/\D/,'')})}}
                                        value={this.state.pincode}
                                    />
                                </FormGroup>

                    </div>
                    <div className="col-md-6">
                        <FormGroup>
                            <label for="date">Date Added</label>
                            <input
                                type="date"
                                className="form-control"
                                placeholder="Date"
                                name="date"
                                id="date"
                                onChange={this.onChange}
                                value={this.state.date}
                            />
                        </FormGroup>
                    </div>
                </div>

                <div className="text-center container">
                    <Button type="submit" color="primary">Add Course</Button>
                    {/*
                        <Button className="ml-2" onClick={() => {
                        console.log(course.id);
                        }}>Read</Button>
                    */}
                    <Button type="reset" onClick={() => {
                        console.log(this.state);
                        // this.setState({id: "", title:"", description:"",pincode:"",phone:"",date:""});
                        this.setState(initialState);
                    }} color="secondary ml-2">Clear</Button>
                    <br></br>
                    <br></br>
                </div>

            </form>
        </Fragment >
    )
}
}

export default AddCourse;