import { React, Component, useState } from 'react'

import axios from 'axios';
// import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, CardImg, CardTitle } from 'reactstrap';
import {Card,CardSubtitle,CardText,Container, CardBody} from 'reactstrap';
import QuestionCard from '../quiz/QuestionCard';
import { toast, ToastContainer } from 'react-toastify';
class QuizCategory extends Component {


  constructor(props) {
    super(props)
    this.state = {
      isCategorySelected: false,
      categories: [],
      a: " ",
      b: [],
      isDataLoaded: false
    }

    this.getQuizWithFetch = this.getQuizWithFetch.bind(this)

  }


  getQuizWithFetch = async (e) => {
    console.log(e)
    const APIUrl = e;
    // const APIUrl="https://opentdb.com/api.php?amount=10"
    const response = await fetch(APIUrl);
    const jsonData = await response.json();
    this.setState({ a: this.state.a + jsonData.response_code, isDataLoaded: true })
    this.state.b.push(jsonData.results)
    console.log(this.state)

    this.state.b = (jsonData.results)
    this.setState({ isCategorySelected: true })
    return jsonData
  }
  render() {
    // let a = "k"+this.state.a
    // let b= this.state.b

    const categoryList = [{ category: "Films", apiUrl: "https://opentdb.com/api.php?amount=10&category=11" },
    { category: "Entertainment: Video Games", apiUrl: "https://opentdb.com/api.php?amount=10&category=15" },
    { category: "Sports", apiUrl: "https://opentdb.com/api.php?amount=10&category=21" },
    { category: "History", apiUrl: "https://opentdb.com/api.php?amount=10&category=23" },
    { category: "Entertainment: Books", apiUrl: "https://opentdb.com/api.php?amount=10&category=10" },
    { category: "Science: Gadgets", apiUrl: "https://opentdb.com/api.php?amount=10&category=30" },
    { category: "Geography",apiUrl: "https://opentdb.com/api.php?amount=10&category=22"},
    { category: "Science: Computers",apiUrl: "https://opentdb.com/api.php?amount=10&category=18"},
    { category: "Animals",apiUrl: "https://opentdb.com/api.php?amount=10&category=27"}
    
  ]
    const elements = [];
    for (const [index, value] of categoryList.entries()) {

      elements.push(
      <Card className="col-6">
        <CardBody>
          <CardImg />
          <CardTitle style={{cursor:'pointer'}} onClick={e => this.getQuizWithFetch(value.apiUrl)} value={value.apiUrl} >{value.category}</CardTitle>
        </CardBody>
        
      </Card>
      )
    }


    const d = [];
    if (this.state.isDataLoaded) {
      d.push(<div>
        <h1>Hello , Data Loaded </h1> </div>)
    }
    else {
      d.push(<div>
        <p>Please select category to start Quiz </p> </div>)
    }

    if (this.state.isCategorySelected) {
      return (
        <div class="container text-center ">
          <QuestionCard que={this.state.b}> </QuestionCard>
        </div>)
    }
    else {

      return (
        
        <div class="container text-center ">
          <div class="row gy-3 gx-5">
            <div class="col-12">
              {d}
              <div class="card p-5 border bg-light">QUIZ CATEGORY</div>
            </div>
            {elements}
          </div>
        </div>
      )
    }
  }

}

export default QuizCategory



