import React from 'react'
import { Redirect } from 'react-router';
import axios from 'axios';
import "./contact.css";
import {toast,ToastContainer} from 'react-toastify';

const initialState = {
            name: '',
            email: '',
            subject: '',
            message: '',
            nameError :'',
            emailError : '',
            subjectError : '',
            messageError : ''
}

class Contact extends React.Component {
    state = initialState;
    constructor(props) {
        super(props);
        let loggedIn = true;
        const token = localStorage.getItem('token');
        this.state = {
            
        }
        if (token === null) {
            loggedIn = false;
            this.setState({ loggedIn: false });
        } else {
            this.setState({ loggedIn: true });
            loggedIn = true;
        }
        this.state = {
            loggedIn
        }
        this.submitEmail = this.submitEmail.bind(this);
        this.onNameChange = this.onNameChange.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
        this.onMsgChange = this.onMsgChange.bind(this);
        this.onSubjectChange = this.onSubjectChange.bind(this);
    }

    onNameChange(event) {
        if(event.target.value.length > 0){
            this.setState({ name: event.target.value })
            console.log(this.state);

            document.getElementsByName("txtName")[0].classList.remove("markred");
            
        }
        else{
            document.getElementsByName("txtName")[0].classList.add("markred");
            this.setState({name:''});
            toast.error(this.state.nameError, {
                position: 'top-right'
            });
        }
        
    }

    onEmailChange(event) {
        // this.setState({ email: event.target.value })
        if(event.target.value.length > 0){
            console.log(event.target.value)
            this.setState({ email: event.target.value })
            console.log(this.state.email);
            document.getElementsByName("txtEmail")[0].classList.remove("markred");
            // document.getElementsByClassName('cls1').classList.remove('markred');
        }
        else{
            // document.getElementsByClassName('cls1').classList.add('markred');
            document.getElementsByName("txtEmail")[0].classList.add("markred");
            this.setState({email:''});
            toast.error(this.state.emailError, {
                position: 'top-right'
            });
        }
    }

    onSubjectChange(event) {
        // this.setState({ subject: event.target.value })
        if(event.target.value.length > 0){
            console.log(event.target.value.length)
            this.setState({ subject: event.target.value })
            console.log(this.state);
            document.getElementsByName("txtSub")[0].classList.remove("markred");

            // document.getElementsByClassName('cls1').classList.remove('markred');
        }
        else{
            // document.getElementsByClassName('cls1').classList.add('markred');
            this.setState({subjectError:'',subject:''});
            document.getElementsByName("txtSub")[0].classList.add("markred");
            toast.error(this.state.subjectError, {
                position: 'top-right'
            });
        }
    }

    onMsgChange(event) {
        // this.setState({ message: event.target.value })
        if(event.target.value.length > 0){
            this.setState({ message: event.target.value })
            document.getElementsByName("txtMsg")[0].classList.remove("markred");
            // document.getElementsByClassName('cls1').classList.remove('markred');
        }
        else{
            // document.getElementsByClassName('cls1').classList.add('markred');
            document.getElementsByName("txtMsg")[0].classList.add("markred");
            this.setState({message:''});
            toast.error(this.state.messageError, {
                position: 'top-right'
            });
        }
    }

    validate() {
        let nameError = '';
        let emailError = '';
        let subjectError = '';
        let messageError = '';
        const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);
        if (!validEmailRegex.test(this.state.email)) {
            emailError = 'Invalid Email';
        }
        if (!this.state.name) {
            nameError = 'Invalid name';
        }

        if (!this.state.subject) {
            subjectError = 'Invalid Subject';
        }

        if (!this.state.message) {
            messageError = 'Invalid Message';
        }

        if (emailError || nameError || subjectError || messageError) {
            this.setState({ nameError, emailError, subjectError, messageError });
            console.log(this.state);
            return false;
        }
        return true;
    }

    submitEmail(e) {
        e.preventDefault();
        const isValid = this.validate();
        console.log(isValid);
        console.log(this.state);
        this.state.isValid = isValid;
        if (isValid) {
            // document.getElementsByClassName('cls1').classList.remove('markred');
            axios({
                method: "POST",
                url: "/send",
                data: this.state
            }).then((response) => {
                console.log(response);
                    toast.success("Message Sent",{
                        position:'bottom-center'
                    });
                    this.resetForm()
            }).catch((err)=>{
                toast.error(err,{
                        position:'top-right'
                    });
            })
            console.log(this.state);
            this.setState(initialState);
        }else{
            // this.setState(initialState);
            document.getElementsByName("txtName")[0].classList.add("markred");
            document.getElementsByName("txtSub")[0].classList.add("markred");
            document.getElementsByName("txtEmail")[0].classList.add("markred");
            document.getElementsByName("txtMsg")[0].classList.add("markred");
            console.log(this.state);
            // toast.error("Enter valid details",{
            //     position:'top-right'
            // });
        }
    }

    resetForm() {
        this.setState({ name: '', email: '', subject: '', message: '' })
    }

    render() {
        if (this.state.loggedIn === false) {
            <ToastContainer hideProgressBar={true} autoClose={2000} />
            return <Redirect to="/" />
        } else {
            return (
                <div className="container contact-form shadow">
                    <ToastContainer hideProgressBar={true} autoClose={2000} />
                    <div className="contact-image">
                        <img src="https://image.ibb.co/kUagtU/rocket_contact.png" alt="rocket_contact" />
                    </div>
                    <form method="post" onSubmit={this.submitEmail}>
                        <h3>Drop Us a Message</h3>
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group ">
                                    <input type="text" name="txtName" value={this.state.name} className={this.state.name > 0 ? "" : "markred" } onChange={this.onNameChange} className="form-control" placeholder="Enter Name" />
                                    <p style={{fontSize:'12px',color:'red',paddingLeft:'5px',paddingTop:'4px'}}>{this.state.nameError}</p>
                                </div>
                                <div className="form-group">
                                    <input type="text" name="txtEmail" value={this.state.email} onChange={this.onEmailChange} className="form-control" placeholder="Enter Email" />
                                    <p style={{fontSize:'12px',color:'red',paddingLeft:'5px',paddingTop:'4px'}}>{this.state.emailError}</p>
                                </div>
                                <div className="form-group">
                                    <input type="text" name="txtSub" value={this.state.subject} onChange={this.onSubjectChange} className="form-control" placeholder="Enter Subject" />
                                    <p style={{fontSize:'12px',color:'red',paddingLeft:'5px',paddingTop:'4px'}}>{this.state.subjectError}</p>
                                </div>

                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <textarea name="txtMsg" onChange={this.onMsgChange} value={this.state.message} className="form-control" placeholder="Enter a Message" style={{ width: '100%', height: '150px' }}></textarea>
                                    <p style={{fontSize:'12px',color:'red',paddingLeft:'5px',paddingTop:'4px'}}>{this.state.messageError}</p>
                                </div>
                            </div>
                            <div className="col-md-12 text-center">
                                <div className="form-group">
                                    <input type="submit" name="btnSubmit" className="btnContact" value="Send Message" />
                                </div>
                            </div>
                        </div>
                    </form>
                </div>


            )
        }
    }
}

export default Contact;
