import React, { Component } from 'react'
import { AiOutlineRotateLeft } from 'react-icons/ai';
let master= 0;
let kokamMaster = 0;
let kokamgrandMaster = 0;
let orangegrandMaster = 0;
let lemonMaster = 0;
let lemongrandMaster = 0;
let sarbatMaster = 0;
let sarbatgrandMaster = 0;
let orangeCheck1 = 1;
let orangeCheck2 = 2;
let orangeCheck3 = 4;
let orangeCheck4 = 8;
let orangeCheck5 = 16;
let grandMaster = 0;
class CheckBoxes extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            init:0,
            extend10:30,
            checkval:false,
            slider1:1,
            data:{
                orangegrandMaster:0,
                kokamgrandMaster:0,
                lemongrandMaster:0,
                sarbatgrandMaster:0
            }
        }
        this.rangeHandler = this.rangeHandler.bind(this);
        this.onExtend = this.onExtend.bind(this);
    }

    rangeHandler = (e) => {
        if(e.target.type === 'range'){
            this.setState({...this.state,[e.target.name]:e.target.value});
            if(e.target.name.includes("orange")){
                master = Number(e.target.value);
                orangegrandMaster = Number(this.props.mrp) * Number(master);
                this.state.data.orangegrandMaster = orangegrandMaster;
                this.props.data.getBill(this.state.data.orangegrandMaster);
            }
            if((e.target.name).includes('kokam')){
                kokamMaster = Number(e.target.value);
                kokamgrandMaster = Number(this.props.mrp) * Number(kokamMaster);
                this.state.data.kokamgrandMaster = kokamgrandMaster;
                this.props.data.getBillKokam(this.state.data.kokamgrandMaster);
            }
            if((e.target.name).includes('lemon')){
                lemonMaster = Number(e.target.value);
                lemongrandMaster = Number(this.props.mrp) * Number(lemonMaster);
                this.state.data.lemongrandMaster = lemongrandMaster;
                this.props.data.getBillLemon(this.state.data.lemongrandMaster);
            }
            if(e.target.name.includes("sarbat")){
                sarbatMaster = Number(e.target.value);
                sarbatgrandMaster = Number(this.props.mrp) * Number(sarbatMaster);
                this.state.data.sarbatgrandMaster = sarbatgrandMaster;
                this.props.data.getBillSarbat(this.state.data.sarbatgrandMaster);
            }

        }
    }

    onExtend = (e) => {
        // console.log(this.state.extend10);
        const enteredValue = prompt('Enter number to extend limit to:');
        // if(e.target.onDoubleClick){
            // this.state.extend10 = 40;
            // $('#saas').val = 0;
            if(Number(enteredValue)){
                if(enteredValue > 0 && enteredValue <= 100)
                this.setState({extend10:enteredValue,init:0});

            }else{
                this.setState({extend10:30});
            }
        // }else{
            // this.state.extend10 = 30;
            // this.setState({extend10:30});
        // }
        console.log(this.state.extend10);
    }

    onChange = (e) => {
        
        if(e.target.type === 'checkbox'){
            // console.log(master);
            console.log(e.target.name + "  " + e.target.checked);
            if(e.target.checked){
                this.setState({[e.target.name]:e.target.checked});
                if((e.target.name).includes('kokam')){
                    kokamMaster = kokamMaster + Number(e.target.value);
                    kokamgrandMaster = Number(this.props.mrp) * Number(kokamMaster);
                    this.state.data.kokamgrandMaster = kokamgrandMaster;
                    this.props.data.getBillKokam(this.state.data.kokamgrandMaster);
                }
                if((e.target.name).includes('orange')){
                    master = master + Number(e.target.value);
                    orangegrandMaster = Number(this.props.mrp) * Number(master);
                    this.state.data.orangegrandMaster = orangegrandMaster;
                    this.props.data.getBill(this.state.data.orangegrandMaster);
                }
                if((e.target.name).includes('lemon')){
                    lemonMaster = lemonMaster + Number(e.target.value);
                    lemongrandMaster = Number(this.props.mrp) * Number(lemonMaster);
                    this.state.data.lemongrandMaster = lemongrandMaster;
                    this.props.data.getBillLemon(this.state.data.lemongrandMaster);
                }
            } else {
                this.setState({[e.target.name]:e.target.checked});
                if((e.target.name).includes('kokam')){
                    kokamMaster = kokamMaster - Number(e.target.value);
                    kokamgrandMaster = Number(this.props.mrp) * Number(kokamMaster);
                    this.state.data.kokamgrandMaster = kokamgrandMaster;
                    this.props.data.getBillKokam(this.state.data.kokamgrandMaster);
                }
                if((e.target.name).includes('orange')){
                    master = master - Number(e.target.value);
                    orangegrandMaster = Number(this.props.mrp) * Number(master);
                    this.state.data.orangegrandMaster = orangegrandMaster;
                    this.props.data.getBill(this.state.data.orangegrandMaster);
                }
                if((e.target.name).includes('lemon')){
                    lemonMaster = lemonMaster - Number(e.target.value);
                    lemongrandMaster = Number(this.props.mrp) * Number(lemonMaster);
                    this.state.data.lemongrandMaster = lemongrandMaster;
                    this.props.data.getBillLemon(this.state.data.lemongrandMaster);
                }
            }
            // console.log("Total orange:"+orangegrandMaster+"  Total kokam:"+kokamgrandMaster+" Total Lemon:"+lemongrandMaster);
            
            // this.props.data.getBillKokam(this.state.data);
            // console.log(this.state.data);
        }
        
        let {orangeCheck1,orangeCheck2} = this.state;
        
    }

    render() {
        // const {orangeCheck1,orangeCheck2,master} = this.state;
        let textColor = this.props.color;
        let changeColor = this.props.changeColor;
        let names = this.props.names;
        let varr = grandMaster;
        return (
            <div className="container">
                {/* <label style={{color:'black'}}>Item:<span style={{color:textColor}}>{this.props.name}</span> Quantity 1:{this.state.orangeCheck1 ? <span style={{color:changeColor}}>  Added</span> : "  Not added"}
                    <input 
                        type="checkbox"
                        checked={this.state.orangeCheck1}
                        onChange={this.onChange}
                        name={names+"1"}
                        value={orangeCheck1}
                    />
                </label>
                <br />
                <label style={{color:'black'}}>Item:<span style={{color:textColor}}>{this.props.name}</span> Quantity 2:{this.state.orangeCheck2 ? <span style={{color:changeColor}}>  Added</span> : "  Not added"}
                    <input 
                        type="checkbox"
                        checked={this.state.orangeCheck2}
                        onChange={this.onChange}
                        name={names+"2"}
                        value={orangeCheck2}
                    />
                </label>
                <br />
                <label style={{color:'black'}}>Item:<span style={{color:textColor}}>{this.props.name}</span> Quantity 4:{this.state.orangeCheck3 ? <span style={{color:changeColor}}>  Added</span> : "  Not added"}
                    <input 
                        type="checkbox"
                        checked={this.state.orangeCheck3}
                        onChange={this.onChange}
                        name={names+"3"}
                        value={orangeCheck3}
                    />
                </label>
                <br />
                <label style={{color:'black'}}>Item:<span style={{color:textColor}}>{this.props.name}</span> Quantity 8:{this.state.orangeCheck4 ? <span style={{color:changeColor}}>  Added</span> : "  Not added"}
                    <input 
                        type="checkbox"
                        checked={this.state.orangeCheck4}
                        onChange={this.onChange}
                        name={names+"4"}
                        value={orangeCheck4}
                    />
                </label>
                <br />
                <label style={{color:'black'}}>Item:<span style={{color:textColor}}>{this.props.name}</span> Quantity 16:{this.state.orangeCheck5 ? <span style={{color:changeColor}}>  Added</span> : "  Not added"}
                    <input 
                        type="checkbox"
                        checked={this.state.orangeCheck5}
                        onChange={this.onChange}
                        name={names+"5"}
                        value={orangeCheck5}
                    />
                </label> */}

                {/* <div>
                    <input 
                        type="text"
                        readOnly
                        className="form-control"
                        // value={grandMaster}
                    />
                </div> */}
                {/* <button onClick={()=>{}}>getBill</button> */}
                <input 
                    className="form-control"
                    type="range"
                    min="0"
                    max={this.state.extend10}
                    step="1"
                    name={names+"1"}
                    defaultValue="0"
                    onChange={this.rangeHandler}
                />
                <div className="text-left" style={{margin:'0'}} onDoubleClick={this.onExtend}>{
                    (names.includes("orange")) ? this.props.name +": "+ master + " Range between [0 & "+this.state.extend10+"]" : (names.includes("kokam")) ? this.props.name +": "+ kokamMaster + " Range between [0 & "+this.state.extend10+"]" : (names.includes("lemon")) ? this.props.name +": "+ lemonMaster + " Range between [0 & "+this.state.extend10+"]" : (names.includes("sarbat")) ? this.props.name +" :  "+ sarbatMaster + " Range between [0 & "+this.state.extend10+"]" : " 0 "
                }    
                </div>
                <hr/>
            </div>
            
        )
    }
}
export default CheckBoxes;
