import axios from 'axios';
import React from 'react'
import { Redirect } from 'react-router';
import countriesList from "./Countries.json";
// import States from './States.json';
// import cities from './state_city.json';
import statedist from './state_dist.json';
class About extends React.Component {
    constructor(props) {
        super(props);
        let loggedIn = true;
        const token = localStorage.getItem('token');
        if (token === null) {
            loggedIn = false;
            this.setState({ loggedIn: false });
        } else {
            this.setState({ loggedIn: true });
            loggedIn = true;
        }
        this.state = {
            loggedIn,
            data:[],
            got:false,
            zipcode:''
        }
        this.submitForm = this.submitForm.bind(this);
        this.selectChange = this.selectChange.bind(this);
        this.citychange = this.citychange.bind(this);
    }

    componentDidMount(){
        fetch(`./Countries.json`, {
            headers : { 
              'Content-Type': 'application/json',
              'Accept': 'application/json'
             }
      
          })
          .then((response) => response.json())
          .then((messages) => {console.log("messages");});

          for(var i in statedist){
            // console.log("<<<<<<"+statedist[i].state+">>>>>>");
            // console.log("districts:");
            //   for(var j in statedist[i].districts){
            //     console.log("        "+statedist[i].districts[j]);
            //   }
            //   console.log(statedist[i].state);
          }
    }    

    // componentDidMount(){
    //     axios.get("https://mocki.io/v1/d4867d8b-b5d5-4a48-a4ab-79131b5809b8").then((res)=>{
    //         this.setState({data:res.data,got:true});
    //         alert("data got");
    //         console.log(this.state);

    //     }).catch((err)=>{
    //         alert(err);
    //         this.setState({data:[],got:false});
    //     })
    // }

    selectChange(value){
        // $("#validationCustom04").children[$("#validationCustom04").selectedIndex].value
        console.log(value);
        const cont = document.getElementById("citySelect");
        cont.innerHTML = "";
        let citiesOptions="";
            while(cont.firstChild){
                cont.removeChild(cont.firstChild);
            }
        if(value){
            citiesOptions = "<option disabled>Select City</option>";
            for(var i in statedist){
                if(statedist[i].state===value){
                    for(var j in statedist[i].districts){
                        citiesOptions += "<option>"+statedist[i].districts[j]+"</option>";
                    }
                }
                // citiesOptions += "<option>"+statedist[i][i]+"</option>";
            }
            this.state.citiesOptions = citiesOptions;
            document.getElementById("citySelect").innerHTML = citiesOptions;
            document.getElementById("citySelect").selectedIndex = 0;
            // console.log(this.state.citiesOptions);
            
        }else{
            document.getElementById("citySelect").innerHTML = "";
        }
        // let selected = document.getElementById("validationCustom04");
        // let newval = selected.children[(selected.selectedIndex)].getAttribute("value");
        // let code = selected.children[(selected.selectedIndex)].getAttribute("code");
        // console.log("selected option: "+ newval);
    }


    citychange(value){
        const val = document.getElementById("validationCustom04").value;
        console.log(val +"  >>  "+value);
    }

    submitForm(e) {
        // Example starter JavaScript for disabling form submissions if there are invalid fields
        e.preventDefault();
        

            // Fetch all the forms we want to apply custom Bootstrap validation styles to
            var forms = document.querySelectorAll('.needs-validation')

            // Loop over them and prevent submission
            Array.prototype.slice.call(forms)
                .forEach(function (form) {
                    form.addEventListener('submit', function (event) {
                        if (!form.checkValidity()) {
                            event.preventDefault()
                            event.stopPropagation()
                        }

                        form.classList.add('was-validated')
                    }, false)
                })
    }

    render() {
        if (this.state.loggedIn === false) {
            return <Redirect to="/" />
        } else {
            return (
                // <div className="text-center" style={{color:'white'}}>
                //     <h1>About Us</h1>
                // </div>
                <div>
                    <form className="row g-3 needs-validation" novalidate onSubmit={this.submitForm}>
                        <div className="col-md-4">
                            <label for="validationCustom01" className="form-label">First name</label>
                            <input type="text" className="form-control" id="validationCustom01" placeholder="Enter firstname" />
                            <div className="valid-feedback">
                                Looks good!
    </div>
                        </div>
                        <div className="col-md-4">
                            <label for="validationCustom02" className="form-label">Last name</label>
                            <input type="text" className="form-control" id="validationCustom02" placeholder="Enter lastname" />
                            <div className="valid-feedback">
                                Looks good!
    </div>
                        </div>
                        <div className="col-md-4">
                            <label for="validationCustomUsername" className="form-label">Username</label>
                            <div className="input-group has-validation">
                                <span className="input-group-text" id="inputGroupPrepend">@</span>
                                <input type="text" className="form-control" id="validationCustomUsername" aria-describedby="inputGroupPrepend" />
                                <div className="invalid-feedback">
                                    Please choose a username.
      </div>
                            </div>
                        </div>
                        {/* <div className="col-md-6">
                            <label for="validationCustom03" className="form-label">City</label>
                            <input type="text" className="form-control" id="validationCustom03" />
                            <div className="invalid-feedback">
                                Please provide a valid city.
                            </div>
                        </div> */}
                            <div className="col-md-3">
                                <label for="validationCustom04" className="form-label">State</label>
                                <select className="form-control" id="validationCustom04" onChange={(e)=>this.selectChange(e.target.value)}>
                                    <option selected disabled value="">Select State</option>
                                    {/* <option>...</option> */}
                                    {
                                        statedist.map((States)=>{
                                            return <option value={States.state}>{States.state}</option>
                                        })
                                    }
                                </select>
                                <div className="invalid-feedback">
                                    Please select a valid state.
                            </div>
                            </div>

                            <div className="col-md-3">
                                <label for="citySelect" className="form-label">City</label>
                                <select className="form-control" id="citySelect" onChange={(e)=>this.citychange(e.target.value)}>
                                    <option selected disabled value="">Select City</option>
                                    {/* {
                                        States.map((States)=>{
                                            return <option value={States.name}>{States.name}</option>
                                        })
                                    } */}                                
                                </select>
                                <div className="invalid-feedback">
                                    Please select a valid state.
                            </div>
                            </div>

                        <div className="col-md-3">
                            <label for="validationCustom05" className="form-label">Zip</label>
                            <input type="tel" className="form-control" id="validationCustom05" value={this.state.zipcode} onChange={(e)=>{this.setState({zipcode:e.target.value.replace(/\D/,'')})}} placeholder="Enter zipcode" />
                            <div className="invalid-feedback">
                                Please provide a valid zip.
                        </div>
                        </div>
                        <div className="col-12">
                            <div className="form-check">
                                <input className="form-check-input" type="checkbox" value="" id="invalidCheck" />
                                <label className="form-check-label" for="invalidCheck">
                                    Agree to terms and conditions
      </label>
                                <div className="invalid-feedback">
                                    You must agree before submitting.
      </div>
                            </div>
                        </div>
                        <div className="col-12">
                            <button className="btn btn-primary" type="submit">Submit form</button>
                        </div>
                    </form>
                </div>

            )
        }
    }
}

export default About;
