import axios from 'axios';
import { React, Component } from 'react'
import { Redirect, Route } from 'react-router'
import QuizCategory from './QuizCategory';
let id;
class QuestionCard extends Component {
    constructor(props) {
        super(props)
        id = Number(localStorage.getItem('id'));

        this.state = {

            q_index: 1,

            question: [],

            userAnswer: [],

            userScore: 0,

            quizEnd: false,
            changeto:false

        }
        this.handleClick = this.handleClick.bind(this)
        this.reloadGame = this.reloadGame.bind(this)
        this.onWhich = this.onWhich.bind(this)
    }


    handleClick(e) {
        // console.log(showCat);
        console.log("q_index---->>>", this.state.q_index)
        console.log("userAns---->>>", this.state.userAnswer)
        console.log("score---->>>", this.state.userScore)
        console.log("targetvalue---->>>", e.target.value)

        if (e.target.value === "true") {
            this.setState({ userScore: this.state.userScore + 1 })
        }

        this.setState({ userAnswer: [...this.state.userAnswer, e.target.innerText] })

        if (this.state.q_index <= 8) {
            this.setState({ q_index: this.state.q_index + 1 })
        }
        else {
            this.setState({ quizEnd: true })
        }
    }



    reloadGame(e) {
        this.setState({

            q_index: 1,

            question: [],

            userAnswer: [],

            userScore: 0,

            quizEnd: false,
            changeto:false

        })

    }

    onWhich(){
        this.setState({changeto:true})
    }

    updateScore=(id,userScrore)=>{
        let data = { id, userScrore }
        axios.post("/updateScore",data).then((res)=>{
            if(res.status===200){
                console.log("updated score");
            }
        }).catch((err)=>{
            console.log(err);
        })
    }

    render() {
        if(this.state.changeto===true){
            return(
                <Route component={QuizCategory}></Route>
            )
        }
        if(this.state.quizEnd){
            console.log(this.state.quizEnd);
            this.updateScore(id,this.state.userScore);
        }

        if(id===null){
            return(
                <Redirect to="/quizCat" />
            )
        }else{


        this.question = this.props.que

        // alist contains all the options correct+incorrect
        const alist = []
        alist.push({ value: this.question[this.state.q_index].correct_answer, isCorrect: true })
        for (const [index, value] of this.question[this.state.q_index].incorrect_answers.entries()) {
            alist.push({ value: value, isCorrect: false })

        }

        function shuffleArray(array) {
            let i = array.length - 1;
            for (i; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                const temp = array[i];
                array[i] = array[j];
                array[j] = temp;
            }
            return array;
        }

        var randomItem = shuffleArray(alist);
        const options_element = [];
        for (const [index, value] of randomItem.entries()) {
            options_element.push(<div><button id="cs1" className=" answer-box container p-3" onClick={this.handleClick} value={value.isCorrect} dangerouslySetInnerHTML={{ __html: value.value }}>
            </button><br></br>
                <br /></div>)
        }



        const quizElement = []
        if (this.state.quizEnd) {
            quizElement.push(
                <div class="container text-center ">

                    <div class="col-12">
                        <div className="btn card p-5 border bg-info"> Score : {this.state.userScore}</div>
                        <a className="btn card p-5" onClick={this.reloadGame}> Reload Quiz</a>
                        <a className="btn card p-5" onClick={this.onWhich}> Change Quiz Category</a>

                    </div>


                </div>


            )


        }


        else {

            quizElement.push(<div>
                <div className="container row header-box" >
                    <h1 className="col-md-12 text-center p-1 text-info">Quiz</h1>
                    <h4 className="col-md-12 text-right  text-info">Score : {this.state.userScore}</h4>
                </div>

                <div className="container p-5 question-box q-b bg-light"> {this.state.q_index} {this.question[this.state.q_index].question} </div>
                <br />
                {options_element}
            </div>)
        }


        // const e= alist[2]
        return (
            <div>
                {quizElement}
            </div>)

        }
    }

}

export default QuestionCard